import React, { useState } from "react";
import { useRef } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";

function ExchangePage() {
  const [fromAmount, setFromAmount] = useState(0);
  const [fromCurrency, setFromCurrency] = useState('USD');
  const [toAmount, setToAmount] = useState(0);
  const [toCurrency, setToCurrency] = useState('TRY');
  const [isResShown, setIsResShown] = useState(false);
  const inputRef = useRef(null);
  const { t } = useTranslation();

  const sell = 0.1;
  const buy = 10;

  const handleSubmit = (e) => {
    e.preventDefault();
    const amount = inputRef.current.value;
    console.log(inputRef.current.setCustomValidity('xx'))
    if (fromCurrency === 'TRY') {
      setFromAmount(amount);
      setToAmount(amount * sell);
      setIsResShown(true);
    } else if (toCurrency === 'TRY') {
      setFromAmount(amount);
      setToAmount(amount * buy);
      setIsResShown(true);
    } else {
    }
  };

  const handleFromChange = (e) => {
    setIsResShown(false);
    if (e.target.value !== 'TRY') {
      setToCurrency('TRY');
      setFromCurrency(e.target.value);
    }
  };

  const handleToChange = (e) => {
    setIsResShown(false);
    if (e.target.value !== 'TRY') {
      setFromCurrency('TRY');
      setToCurrency(e.target.value);
    }
  };

  const res = `${fromAmount} ${fromCurrency} = ${toAmount} ${toCurrency}`
  return (
    <section className="content__section content__section_coins flex flex-column flex-center">
      <div className="content__title">currencies</div>
      <div>rates</div>
      <div className="content__text form flex flex-column flex-center">
        <form
          className="form__content flex flex-column flex-center"
          onSubmit={(e) => handleSubmit(e)}
        >
          <div className="form__select-container flex">
            <div className="form__select-group flex flex-column">
              <label>from</label>
              <select className="form__select" onChange={e => handleFromChange(e)} value={fromCurrency}>
                <option defaultValue>USD</option>
                <option>RUB</option>
                <option>EUR</option>
                <option>TRY</option>
              </select>
            </div>
            <div className="form__select-group flex flex-column">
              <label>to</label>
              <select className="form__select flex flex-center" onChange={e => handleToChange(e)} value={toCurrency}>
                <option>USD</option>
                <option>RUB</option>
                <option>EUR</option>
                <option defaultValue>TRY</option>
              </select>
            </div>
          </div>
          <input className="form__input" type="number" ref={inputRef} min={1} placeholder="amount" required />
          <button className="form__btn">get rate</button>
        </form>
        <div className={`content__text ${!isResShown && "content__text_hidden"} flex flex-column flex-center`}>{res}</div>
      </div>
      <Link to="/" className="content__text flex flex-column flex-center">
        back
      </Link>
    </section>
  );
}

export default ExchangePage;
